
import Scroll from '@/components/scroll/scroll'
import productImg from '@/assets/img/specialLocal-product.png'
import VHeader from '@/components/v-header/v-header'
import Share from '@/components/share/share'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Icon from 'vux/src/components/icon/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


import specialLocalProductInfo from '@/pages/cart/cart-page/specialLocal-product/specialLocal-product-tab/specialLocal-product-info/specialLocal-product-info'
import specialLocalProductDetail from '@/pages/cart/cart-page/specialLocal-product/specialLocal-product-tab/specialLocal-product-detail/specialLocal-product-detail'
import specialLocalProductComment from '@/pages/cart/cart-page/specialLocal-product/specialLocal-product-tab/specialLocal-product-comment/specialLocal-product-comment'
import VoiceIcon from '@/assets/img/voiceIcon.png'

export default {
data () {
return {
productImg: productImg,
opacity: 1,
tabList: ['商品', '详情', '评论'],
showShare: false,
num: 0,
marginTop: 0,
voiceIcon: VoiceIcon,
toggle: false
}
},
props: {
productName: {
type: String,
default: ''
}
},
directives: {
TransferDom
},
components: {
Scroll,
VHeader,
Share,
Tab,
TabItem,
specialLocalProductInfo,
specialLocalProductDetail,
specialLocalProductComment,
Popup,
Icon,
XNumber,
XButton
},
mounted () {
this.$refs.verticalScroll.$el.style.height = window.innerHeight + 'px'
this.marginTop = this.$refs.header.$el.offsetHeight
},
methods: {
toShare() {
this.showShare = true
},
numChange (index) {
this.num = index
},
toggleBtn () {

this.toggle = true
},
toggleBtnCantel () {
this.toggle = false
},
toAI () {
this.$router.push('/chatAI')
}
}
}



