import Scroll from '@/components/scroll/scroll'
import SpecialProduct from '@/assets/img/detailsView.jpg'

export default {
  data () {
    return {
      SpecialProduct: SpecialProduct
    }
  },
  components: {
    Scroll
  },
  mounted () {
    this.$refs.verticalScroll.$el.style.height = (window.innerHeight - 150) + 'px'
  }
}



